.page {
  text-align: center;
  position: relative;
  min-height: 100vh;
}

.content {
  padding-bottom: 5rem;
}

.accomplishments {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.credit {
  position: absolute;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  bottom: 0;
}